import React, { Component } from 'react'
import { Route, Switch } from 'react-router-dom';

import Invoice from './Invoice';
import Home from './Home';
import Expense from './Expense';
import Bank from './Bank';
export default class Main extends Component {
  render() {
    return (
      <main className='main'> 
        <Switch>
          <Route exact path='/accountant-app/' component={Home} />
          <Route  path='/accountant-app/invoice' component={Invoice} />
          <Route  path='/accountant-app/expense' component={Expense} />
          <Route  path='/accountant-app/bank' component={Bank} />
        </Switch>
      </main>
    )
  }
}
